import React, { Fragment } from "react";
import { Link } from 'react-router-dom'


const Navegation = () => {
  return (
    <Fragment>
      <div uk-sticky="sel-target: .uk-navbar-container; cls-active: uk-navbar-sticky; bottom: #transparent-sticky-navbar">
        <nav className="uk-navbar-container uk-padding uk-padding-remove-vertical uk-animation-slide-top-small" uk-navbar="true">

            <div className="uk-navbar-left">
              <Link className="uk-navbar-toggle" to="#" uk-toggle="target: #offcanvas-nav-primary">
                  <span uk-navbar-toggle-icon="true" uk-tooltip="title: Menú; pos: bottom"></span>
              </Link>
            </div>
            <div className="uk-navbar-center">
              <Link to="#" className="uk-navbar-item uk-logo">
                <img src="static/images/logo.png" alt="logo" width="100"/>
              </Link>
            </div>
            <div className="uk-navbar-right">
              <ul className="uk-iconnav">
                  <li className="uk-margin-small-right"><Link to="#" uk-icon="icon: bell; ratio: 1.2" uk-tooltip="title: Notificaciones; pos: bottom"></Link></li>
              </ul>
            </div>

        </nav>
      </div>
    </Fragment>
  );
};

export default Navegation;
