import React, { Fragment } from "react";
import DataTable from "react-data-table-component";
import DataTableExtensions from 'react-data-table-component-extensions';

import { connect } from 'react-redux'
import getProductosActions from '../redux/actions/productosActions'

const DataTableProductos = (props) => {
  
  const columns = [
    {
      name: "FOTO",
      selector: "Foto",
      sortable: true
    },
    {
      name: "CODIGO",
      selector: "Codigo",
      sortable: true
    },
    {
      name: "PRODUCTO",
      selector: "Producto",
      sortable: true
    },
    {
      name: "DETALLE",
      selector: "Detalle",
      sortable: true,
    },
    {
      name: "PRECIO",
      selector: "Precio",
      sortable: true,
    },
    {
      name: "RUBRO",
      selector: "Rubro",
      sortable: true
    },
    {
      name: "SUBRUBRO",
      selector: "SubRubro",
      sortable: true
    }
  ];

  const tableData = {
    columns,
    data: props.productos
  };
  
  const ExpanableComponent = ({ data }) => (
    <table className="uk-table uk-table-middle uk-table-small">
      <tbody className="uk-text-meta uk-text-justify">
          <tr>
              <td>Codigo: {data.Codigo}</td>
              <td>Producto: {data.Producto}</td>
              <td>Descripción: {data.Detalle}</td>
          </tr>
          <tr>
              <td>Foto: {data.Foto}</td>
              <td>Precio: {data.Precio}</td>
              <td>Rubro: {data.Rubro}</td>
              <td>SubRubro: {data.SubRubro}</td>
          </tr>
      </tbody>
    </table>
  );
  
  return (
    <Fragment>

      { props.fetching ? (<span uk-spinner="ratio: 4.5"></span>) :  
      
      <DataTableExtensions className="uk-margin-large-bottom"
        {...tableData}
      >
      <DataTable
        noHeader={true}
        defaultSortField="id"
        defaultSortAsc={false}
        pointerOnHover={true}
        pagination
        highlightOnHover
        expandableRows
        expandableRowsComponent={<ExpanableComponent />}
      />
      </DataTableExtensions>
      }
    </Fragment>
  );
};

const mapStateToProps = (state) => {
  return {
    productos: state.productos.array,
    fetching: state.productos.isFetching
  };
};

const mapDispatchToProps = (dispatch) => {
  dispatch(getProductosActions)
  return {
    fetchProductos: () => { dispatch(getProductosActions) }
  };
};

export default connect(mapStateToProps,mapDispatchToProps)(DataTableProductos)