import React, { Fragment } from "react";
import {Link} from 'react-router-dom'
import { connect } from 'react-redux'

import Navegation from '../components/Navegation'
import ImportProductos from '../components/ImportProductos';
import ImportImgProductos from '../components/ImportImgProductos';
import DataTableProductos from '../components/DataTableProductos';

const Clientes = (props) => {

  return (
    <Fragment>
        <Navegation/>

        <div className="uk-grid-small uk-child-width-expand@s uk-text-center uk-margin-medium uk-padding-remove-vertical uk-animation-fade" uk-grid="true">
          <div>
            <div className="uk-container uk-container-expand">
              <h2 className="uk-margin-small uk-text-uppercase uk-text-bold">Mis Productos</h2>
              <h4 className="uk-margin-small">Gestioná todos tus productos sin perder tiempo!..</h4>
            </div>
          </div>
        </div>

        <div className="uk-grid uk-child-width-1-1@s uk-text-center uk-padding-large uk-padding-remove-top uk-animation-fade" uk-grid="true">
          <div>
          <ul className="uk-flex-center uk-margin-remove-vertical" uk-tab="connect: #component-tab-left; animation: uk-animation-slide-left-medium, uk-animation-slide-right-medium">
                  <li className="uk-text-bold"><Link to="#">LISTAR</Link></li>
                  <li className="uk-text-bold"><Link to="#">IMP. Excel</Link></li>
                  <li className="uk-text-bold"><Link to="#">IMP. Imagen</Link></li>
              </ul>
          <div className="uk-card uk-card-default uk-card-hover uk-card-body">
            <ul id="component-tab-left" className="uk-switcher">
                <li><DataTableProductos/></li>
                <li><ImportProductos/></li>
                <li><ImportImgProductos/></li>
            </ul>
          </div>
          </div>
        </div>

        <div id="offcanvas-nav-primary" uk-offcanvas="mode: push;overlay: true">
            <div className="uk-offcanvas-bar uk-flex uk-flex-column">
                <ul className="uk-nav uk-nav-primary uk-nav-center uk-margin-auto-vertical">
                    <li className="uk-active">
                    <img className="uk-border-circle uk-padding-small" src="../static/images/avatar.jpg" width="200" height="200" alt="Border circle"/>
                    </li>
                    <li className="uk-nav-header">Menú</li>
                    <li><Link to="#"> Mi Perfil</Link></li>
                    <li><Link to="#"> Suscripción</Link></li>
                    <li><Link to="#"> Configuración</Link></li>
                    <li className="uk-nav-divider"></li>
                    <li><Link to="#">Cerrar Sesión</Link></li>
                </ul>
            </div>
        </div>
    </Fragment>
  );
};

export default connect((state) => {
  return state
})(Clientes)
