import React, { useState, Fragment } from "react";
import { useDropzone } from "react-dropzone";
import { connect } from "react-redux";
import axios from "axios";

const ImportProductosImg = () => {
  const [isFetching, setFetching] = useState(false);
  const [istoggle, setToggle] = useState({
    toggle: false,
    msg: null,
    type: null
  });

  const {
    acceptedFiles,
    rejectedFiles,
    getRootProps,
    getInputProps,
    isDragActive
  } = useDropzone({
    accept: ".jpg, .png .jpeg",
    multiple: true,
    onDrop: acceptedFiles => {
      setToggle({
        toggle: false
      });
      const formData = new FormData();
      acceptedFiles.forEach(async file => {
        formData.append("file", file);
        await axios
          .post(
            "https://www.sundelimpieza.com/api/imagenes/importar",
            formData
          ) // https://www.sundelimpieza.com/api/
          .then(res => {
            setFetching(true);

            setTimeout(() => {
              setToggle({
                toggle: true,
                msg: res.data,
                type: "uk-alert-success"
              });
            }, 3000);
          })
          .catch(err => {
            setToggle({
              toggle: true,
              msg: "No fue posible realizar la importación",
              type: "uk-alert-danger"
            });
          })
          .finally(() => {
            setTimeout(() => {
              setFetching(false);
            }, 3000);
          });
      });
    }
  });

  const acceptedFilesItems = acceptedFiles.map(file => (
    <li key={file.path}>
      {file.path} - {file.size} bytes
    </li>
  ));

  const rejectedFilesItems = rejectedFiles.map(file => (
    <li key={file.path}>
      {file.path} - {file.size} bytes
    </li>
  ));

  const { toggle, msg, type } = istoggle;

  return (
    <Fragment>
      <div className="uk-width-1-1 uk-text-center">
        {toggle && (
          <div className={type + " uk-alert"} uk-alert="true">
            <p>{msg}</p>
          </div>
        )}
      </div>

      <div {...getRootProps()}>
        <input {...getInputProps()} />
        <div className="js-upload uk-placeholder uk-text-center">
          {isFetching ? (
            <span uk-spinner="ratio: 2.3"></span>
          ) : (
            <span
              className="uk-color-pink-bold"
              uk-icon="icon: cloud-upload; ratio: 3.5"
            ></span>
          )}
          {isDragActive && (
            <h6 className="uk-card-title uk-color-pink-bold uk-text-bold">
              Suelte el achivo aquí...
            </h6>
          )}
          {!isDragActive && (
            <h6 className="uk-card-title uk-color-pink-bold uk-text-bold">
              Arrastre y suelte un archivo aquí..
            </h6>
          )}
        </div>
      </div>
      <div
        className="uk-grid-collapse uk-child-width-expand@s uk-margin-small-top uk-text-center"
        uk-grid="true"
      >
        <div>
          <p className="uk-text-small uk-text-bold uk-margin-small-top">
            Archivos Aceptados
          </p>
          <ul className="uk-list uk-list-large uk-list-divider uk-text-small">
            {acceptedFilesItems}
          </ul>
        </div>
        <div>
          <p className="uk-text-small uk-text-bold uk-margin-small-top">
            Archivos Rechazados
          </p>
          <ul className="uk-list uk-list-large uk-list-divider uk-text-small">
            {rejectedFilesItems}
          </ul>
        </div>
      </div>
    </Fragment>
  );
};

export default connect(state => {
  return state;
})(ImportProductosImg);
